import React from 'react'
import './index.less'
import NoneData from 'components/NoneData/all-none'
import connect from 'connect'
import { Checkbox, Flex, NavBar, WhiteSpace, WingBlank, Button, List, Grid, Card, Stepper, Modal } from 'antd-mobile';
const alert = Modal.alert;
@connect
class Home extends React.Component {
    constructor(props){
        super(props);
        
        this.state = {
          navTop: false,
          swiper: null,
          value: '美食',
          data: ['1', '2', '3'],
          imgHeight: 176,
          disabled: false,
          val: 2,
          manage: true,
          allcehck:false
        }

        this.$tab = null;
        this.offsetTop = 0;
    }

    
    componentDidMount () {
       setTimeout(() => {
             this.setState({
               data: ['AiyWuByWklrrUDlFignR', 'TekJlZRVCjLFexlOCuWn', 'IJOtIlfsYdTyaDTRVrLI'],
             });
           }, 100);
    }


      onChange= (value) => {
        this.setState({ value });
      };
      clear = () => {
        this.setState({ value: '' });
      };
      handleClick = () => {
        this.manualFocusInst.focus();
    }

    renderTabBar(props) {
      return (<div>
        {({ style }) => <div style={{ ...style, zIndex: 1 }}><Tabs.DefaultTabBar {...props} /></div>}
      </div>);
    }

    onChange2 = (val) => {
        // console.log(val);
        this.setState({ val });
      }
    onChange1 = (val1) => {
        // console.log(val);
        this.setState({ val1 });
    }
    toggle() {
      this.setState({ manage : !this.state.manage });
    }

    allcheck() {
      this.setState({ allcehck : !this.state.allcehck });
    }

    del() {
      if(!this.state.manage) {
        alert('温馨提示', '您确定要删除选中的商品吗？', [
          { text: '取消', onPress: () => {console.log('cancel')} },
          { text: '确定', onPress: () => {console.log('ok')} },
        ])
      }
      

    }


    render () {
      const data = [
        {
          img: require('../../images/lian.png'),
          title: 'Meet hotel',
          des: '不是所有的兼职汪都需要风吹日晒',
          value: 0,
        },
        {
          img: require('../../images/lian.png'),
          title: 'McDonald\'s invites you',
          des: '不是所有的兼职汪都需要风吹日晒',
          value: 1,
        },
      ];

      const data22 = [
            { value: 0, label: 'Ph.D.' },
            { value: 1, label: 'Bachelor' },
            { value: 2, label: 'College diploma' },
          ];
        const Item = List.Item;
        const Brief = Item.Brief;
        const CheckboxItem = Checkbox.CheckboxItem;
        const AgreeItem = Checkbox.AgreeItem;
        return (
            <div className="gouwuche-container">
              <NavBar
                   mode="light"
                   rightContent={<span onClick={() => {this.toggle()}}>{this.state.manage ? '管理' : '完成'}</span>}
                 >购物车</NavBar>
              <List>
                {data.map((obj, index) => (
                  <CheckboxItem key={obj.value} onChange={() => this.onChange(obj.value)}  checked={this.state.allcehck} key={index}>
                    <div>
                      <div style={{ display: '-webkit-box', display: 'flex', padding: '15px 0' }}>
                        <img style={{ height: '0.75rem', width: '1rem', marginRight: '15px' }} src={obj.img} alt="" />
                        <div style={{ lineHeight: '0.25rem' }}>
                          <div className="destext">{obj.des}</div>
                          <div><span className="hongtext">￥20000.00</span><span className="numtextdes">今日价</span></div>
                          <div><span className="numtextdes">数量</span>
                          <Stepper
                            style={{ width: '0.96rem' }}
                            showNumber
                            max={10}
                            min={1}
                            value={this.state.val}
                            onChange={this.onChange2}
                          />
                          </div>
                        </div>
                      </div>
                    </div>
                  </CheckboxItem>
                ))}
              </List>
              <CheckboxItem key="all" data-seed="logId" className="allcell" onClick={() => {this.allcheck()}}>
                  <div style={{ display: '-webkit-box', display: 'flex'}}>
                  <span className="bottomall">全选</span>
                  <span className="heji">合计：</span>
                  <span className="xiaojia">￥99.66</span>
                  <Button type="warning" onClick={() => {this.del()}}>{this.state.manage ? '结算' : '删除'}</Button>
                </div>
              </CheckboxItem>
            </div>
        )
    }
}
export default Home


